import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Button,
  Input,
  FormGroup,
  Label,
  Table,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Classes from "./shop.module.css";
import Spinner from "reactstrap/lib/Spinner";

const Brand = (props) => {
  const [values, setValue] = useState({ title: "", label: "" });
  const [mainCategoryFromServer, setMainCategoryFromServer] = useState([]);
  const [subCategoryFromServer, setSubCategoryFromServer] = useState([]);
  const [allBrandFromServer, setAllBrandFromServer] = useState([]);
  const [arrayHolder, setArrayHolder] = useState([]);
  const [image, setImage] = useState("");
  const [file, setFile] = useState("");
  const [submitBrandLoading, setSubmitBrandLoading] = useState(false);
  const [loading, setLoading] = useState(false);

  
  useEffect(() => {
    //all category
    setLoading(true);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: true,
          hasParent: false,
          catId: null,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setMainCategoryFromServer(getAllCategory);
      })
      .catch((err) => console.log(err));

    //all brands
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
          query getAllBrand($page:Int,$limit:Int,$getAll:Boolean,$catId:ID){
            getAllBrand(input:{page:$page,limit:$limit,getAll:$getAll,catId:$catId}){
              name,
              label,
              category{
                _id,
                name
              },
              image
            }
          }`,
        variables: {
          page: 1,
          limit: 30,
          getAll: true,
          catId: null,
        },
      },
    })
      .then((res) => {
        setLoading(false);
        //all category
        const { getAllBrand } = res.data.data;
        setAllBrandFromServer(getAllBrand);
        console.log(getAllBrand);
      })
      .catch((err) => console.log(err));
  }, []);

  const changeLabelvaTitle = (e) => {
    const { name, value } = e.target;
    setValue((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmitMain = (e) => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: true,
          catId: e.target.value,
        },
      },
    })
      .then((res) => {
        //all category
        const { getAllCategory } = res.data.data;
        setSubCategoryFromServer(getAllCategory);
      })
      .catch((err) => console.log(err));
  };

  const handleSubCat = (e) => {
    const indx = subCategoryFromServer.findIndex((item) => {
      return item._id === e.target.value;
    });
    // value ( روش دوم)
    setArrayHolder((prev) => [...prev, subCategoryFromServer[indx]]);

    //(روش اول)
    const temp = [...subCategoryFromServer];
    temp.splice(indx, 1);
    setSubCategoryFromServer(temp);
  };

  const removeArrayHolder = (item, index) => {
    const copy = [...arrayHolder];
    copy.splice(index, 1);
    setArrayHolder(copy);

    const res = [...subCategoryFromServer];
    res.push(item);
    setSubCategoryFromServer(res);
  };

  const handlerImage = (e) => {
    setFile(e.target.files[0]);
    const privew = URL.createObjectURL(e.target.files[0]);
    setImage(privew);
  };

  const handleSubmit = () => {

    switch (true) {
      case values.title === "":
        toast.warn("فیلد عنوان برند نباید خالی باشد");
        return false;

      case arrayHolder.length === 0:
        toast.warn("دسته دوم را مشخص کنید");
        return false;

      case file === "":
        toast.warn("تصویر را مشخص کنید");
        return false;

      default:
        break;
    }

    setSubmitBrandLoading(true);
    const arr = [];
    arrayHolder.map((item) => arr.push(item._id));
    console.log(arr);

    let data = {
      query: `
        mutation addbrand($name:String!,$label:String,$category:[ID!]!,$image:Upload!){
          brand(input:{name:$name,label:$label,category:$category,image:$image}){
            status,
            message
          }
        }
      `,
      variables: {
        name: values.title,
        label: values.label,
        category: arr,
        image: null,
      },
    };
    let map = {
      0: ["variables.image"],
    };
    const formD = new FormData();
    formD.append("operations", JSON.stringify(data));
    formD.append("map", JSON.stringify(map));
    formD.append(0, file, file.name);

    axios({
      url: "/",
      method: "post",
      data: formD,
    })
      .then((res) => {
        setSubmitBrandLoading(false);
        const { status, message } = res.data.data.brand;
        if (status === 200) {
          toast.success(message);
        } else {
          toast.error(message);
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>برند محصولات</CardHeader>
            <CardBody>
              <FormGroup row>
                <Col xs="6">
                  <FormGroup>
                    <Label htmlFor="title">عنوان</Label>
                    <Input
                      type="text"
                      id="title"
                      placeholder="عنوان برند را وارد نمایید"
                      name="title"
                      value={values.title}
                      onChange={changeLabelvaTitle}
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="6">
                  <FormGroup>
                    <Label htmlFor="label">توضیحات</Label>
                    <Input
                      type="text"
                      id="label"
                      placeholder="توضیحات برند را وارد نمایید"
                      name="label"
                      required
                      value={values.label}
                      onChange={changeLabelvaTitle}
                    />
                  </FormGroup>
                </Col>
                <Col xs="6">
                  <FormGroup>
                    <Label htmlFor="Maincategory">دسته اصلی</Label>
                    <Input
                      type="select"
                      name="Maincategory"
                      id="Maincategory"
                      onChange={handleSubmitMain}
                    >
                      <option></option>
                      {mainCategoryFromServer.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="6">
                  <FormGroup>
                    <Label htmlFor="subcategory">دسته دوم</Label>
                    <Input
                      type="select"
                      name="subcategory"
                      id="subcategory"
                      multiple
                      onChange={handleSubCat}
                    >
                      {subCategoryFromServer.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="6" className={Classes.box}>
                  {arrayHolder.length > 0
                    ? arrayHolder.map((item, index) => (
                        <div className={Classes.boxDetails} key={index}>
                          <span>{item.name}</span>
                          <i
                            className="fa fa-remove fa-lg"
                            onClick={() => removeArrayHolder(item, index)}
                          ></i>
                        </div>
                      ))
                    : null}
                </Col>
                <Col xs="6" className={Classes.centercontain}>
                  <div className={Classes.addFile}>
                    <div className={Classes.addImage}>
                      <label htmlFor="add_media">گزینش تصویر</label>
                    </div>
                    <input
                      type="file"
                      id="add_media"
                      multiple
                      name="add_media"
                      onChange={handlerImage}
                    />
                  </div>
                </Col>
                <Col xs="6">
                  {image ? (
                    <img
                      className={Classes.imageBrand}
                      src={image}
                      alt={image}
                    />
                  ) : null}
                </Col>
              </FormGroup>
            </CardBody>
            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={handleSubmit}
              >
                {submitBrandLoading ? <Spinner size="sm" /> : "ثبت برند"}
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i>همه برند ها
            </CardHeader>
            <CardBody>
              {loading ? (
                <center>
                  <Spinner size="sm" />{" "}
                </center>
              ) : (
                <Table striped responsive size="sm">
                  <thead>
                    <tr>
                      <th>نام برند</th>
                      <th>دسته مرتبط</th>
                      <th>تصویر</th>
                      <th>توضیحات</th>
                    </tr>
                  </thead>
                  <tbody>
                    {allBrandFromServer.map((item) => (
                      <tr>
                        <td>{item.name}</td>
                        <td>
                          {item.category.map((subCat) => (
                            <React.Fragment key={subCat._id}>
                              {subCat.name}
                              <br />
                            </React.Fragment>
                          ))}
                        </td>
                        <td>
                          {" "}
                          <div className={Classes.mediasection}>
                            <img
                              src={`${process.env.REACT_APP_BASE_URL}${item.image}`}
                              alt={item.image}
                            />
                          </div>
                        </td>
                        <td>{item.label}</td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              )}
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Brand;
